import React from 'react'
import marked from "marked";
import data from "../../api/data";
import Section from '../../components/Section';
import * as moment from 'moment';
import { markedInline } from '../../utils/helpers';
import "./Education.scss";
import CVTable from '../../components/CVTable';

function formatDate(str, _default) {
  const date = moment(str, moment.ISO_8601, false);
  if (date.isValid()) {
    return date.format("MMM YYYY");
  }
  return _default;
}

function Educations (props) {
  const {title, educations} = props;
  const items = educations && educations.map((education) => {
    const name = (<React.Fragment>
      <span className="date-from">{formatDate(education.from, education.from)}</span>
      <span className="date-to"> - {formatDate(education.to, education.to)}</span>
    </React.Fragment>);
    const content = (
      <div>
        <span className="degree" dangerouslySetInnerHTML={{ __html: markedInline(education.degree) }}></span>
        <br />
        <span className="institute" dangerouslySetInnerHTML={{ __html: markedInline(education.university) }} />
        {education.location}
        <br />
        {/* {marked(education.content)} */}
        <div dangerouslySetInnerHTML={{ __html: marked(education.content) }}/>
      </div>
    );
    return {name, content};
  })
  return (
    <Section id="education">
      <h2 dangerouslySetInnerHTML={{__html: markedInline(title)}}></h2>
      {educations && (
        <CVTable items={items} className="educations" />
        // <table className="educations">
        //   <tbody>
        //     {educations.map((item, i) => (
        //       <Item education={item} key={i}/>
        //     ))}
        //   </tbody>
        // </table>
      )}
        
    </Section>
  );
}


export default function DefaultEducation(props) {
  const educations = data.education || [];
  return <Educations title='Education' educations={educations} {...props}/>;
}